
@media screen and (max-width: 600px){
    *{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        text-align: center;
    }
    
    h1{
        font-size: 2rem;
        font-weight: 600;
    }
    
    .alpha-wrapper{
        width: 100%;
    }
    .burger-div{
        display: flex;
        flex-direction: column;
        gap: 5px;
        width: 3rem;
        height: 3rem;
        position: absolute;
        top: 2rem;
        right: 2rem;
        cursor: pointer;
        div{
            width: 100%;
            height: 0.2rem;
            background: rgb(228, 228, 228);
        }
    }
    header{
        height: 90vh;
        padding: 1rem;
        nav{
            flex-direction: column;
            padding: 2rem;
            gap: 3rem;
            position: relative;
        }
        img{
            display: block;
            margin-left: -10rem;
        }
        .wrapper{
            background: white;
            //opacity: 0.5;
            //display: flex;
            display: none;
            flex-direction: column;
            width: 5rem;
            height: 80vh;
            width: 100%;
            border-radius: 10px;
            ul{
                display: flex;
                flex-direction: column;
                color: black;
            }
            .last-div{
                display: flex;
                flex-direction: column;
            }
            .sign-up{
                width: 8rem;
                background: linear-gradient(45deg, #ff8e70, #ff4359);
                color: white;
            }
            .login{
                width: 8rem;
                height: 2rem;
                color: black;
            }
        }
    }

    .section-0{
        h1{
            margin: 0.2rem;
        }
        p{
            font-size: 1.2rem;
            font-weight: 300;
        }
    }
    .section-0-btn-div{
        width: 20rem;
        height: 5rem;
        padding: 0;
        p{
            font-size: 0.7rem;
        }
        .section-0-btn-1,
        .section-0-btn-2{
            width: 10rem;
            height: 3rem;
            p{
                font-weight: 600;
                font-size: 0.8rem;
            }
        }
    }
    .section-2{
        padding: 0.7rem;
        //margin-bottom: 5rem;
        h1{
            padding: 1rem;
        }
        .section-2-container{
            flex-direction: column-reverse;
            padding: 0;
            div{
                width: 100%;
                text-align: center;
                img{
                    width: 100%;
                    content: url('./../../images/illustration-editor-mobile.svg');
                }
            }
        }
    }
    .section-2-txt-container{
        width: 100%;
    }
    .section-2-img-container{
        width: 100%;
        height: 100%;
        img{
            width: 100%;
            height: 100%;
        }
    }

    .section-3{
        margin-top: 10rem;
        flex-direction: column;
        height: 40rem;
        padding: 0;
        gap: 0rem;
        div{
            width: 100%;
            padding: 2rem;
        }
        img{
            width:100%;
            //height: 30rem;*/
            margin-top: -10rem;
        }
        h1{
            font-size: 2rem;
        }
    }

    .section-4{
        padding: 0.7rem;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        div{
            width: 100%;
        }
        img{
            display: block;
            margin-left: auto;
            margin-right: auto;
            padding: 0;
            width: 100%;
            height: 100%;
            content: url('./../../images/illustration-laptop-mobile.svg');
        }
    }
}